
















































































































































































export default {
	data() {
		return {
			isShow: false,
			nav2Show:false, //控制左上角点击遮罩
			switchB:true
		};
	},
	methods: {
		coverShow() {
			this.isShow = true
		},
		coverHide() {
			this.isShow = false
		},
		navShowFn() {
			this.nav2Show = true
		},
		navHideFn() {
			this.nav2Show = false
		},
		handleJoinList(){
			uni.navigateTo({
				url:'../home/joinlist'
			})
		},
		SwitchB(e) {
			let that = this;
			uni.showModal({
				confirmColor:'#2DDBC4',
				content:'是否确定关闭该活动?',
				cancelText:'下次再说',
				confirmText:'立即申请',
				cancelColor:'#888',
				success:function(s) {
					console.log(s)
					if(s.confirm){
						that.switchB = false
					}
					if(s.cancel){
						that.switchB = true
					}
					
				},
				fail:function(){
					console.log(2)
					that.switchB = false
				}
			})
			
		}
	},
}
